import * as React from 'react';
import Widget from './widget';
import { isMobile, registerWithMobileRuntime } from '@ab/framework';

// window.MuiABJSBridge = {};
// window.MuiABJSBridge.registerLifeCycleCallback = () => {
//   window.onLaunch({sdfsadsa: "12321 dsf ds dsf dsf ds fsd fsd fds fsd fsd sdfds sd fs"})
// }

export default function Root(props) {
  const [launchOptions, setLaunchOptions] = React.useState({iit: 'init'});

  React.useEffect(() => {
    if (isMobile()) {
      window.addEventListener('onLaunchEvent', ({ detail }) => setLaunchOptions(detail));
      registerWithMobileRuntime()
      return () => {
        window.removeEventListener('onLaunchEvent', setLaunchOptions);
      };        
    }
  }, []);

  
  {isMobile() && console.log(launchOptions)}

  return <Widget profile={props.profile} title={JSON.stringify(launchOptions) } />;
}
