import { Emitter } from '@ab/framework';

const mfId = '@' + process.env.HELIOS_WEB_ORG_NAME + '/' + process.env.HELIOS_WEB_PROJECT_NAME;

const Event = {
  on: (event, fromMfId, fn) => Emitter.on(event, fromMfId, fn),
  once: (event, fromMfId, fn) => Emitter.once(event, fromMfId, fn),
  off: (event, fromMfId, fn) => Emitter.off(event, fromMfId, fn),
  emit: (event, payload, permittedListeners) => Emitter.emit(event, mfId, payload, permittedListeners),
};
Object.freeze(Event);

export default Event;
