import * as React from 'react';
import { Badge, Grid, Card, Button, Typography } from 'material-ui';
import image from './apply-card.png';
import { T } from '@ab/common';
import Event from 'utils/emitter';

export default function UI(props) {
  const theme = T(props?.profile?.theme);

  return (
    <Card raised>
      <Grid
        container
        direction="column"
        justifyContent="space-between"
        style={{ height: '100%' }}
      >
        <Grid item>
          <Typography style={{ padding: 10 }} align="center" gutterBottom>
            {props.title || 'Card Spend Analysis'}
          </Typography>
        </Grid>
        <Grid
          container
          justifyContent="center"
          style={{ minHeight: 100, maxHeight: 300 }}
        >
          <img src={image} alt="image" />
        </Grid>
        <Grid container justifyContent="center">
          <Button
            variant="contained"
            size="small"
            style={{
              background: theme.palette.button,
              paddingLeft: 15,
              paddingRight: 15,
            }}
            onClick={() => Event.emit('CARD_APPLIED', {})}
          >
            Apply Now
          </Button>
        </Grid>
        <Grid
          container
          justifyContent="space-around"
          style={{ background: '#eff6ff', padding: 10, marginTop: 10 }}
        >
          <Button size="small" style={{ color: 'grey' }}>
            <Badge color="secondary" badgeContent=" " variant="dot"></Badge>
            &nbsp;&nbsp;Education
          </Button>
          <Button size="small" style={{ color: 'grey' }}>
            <Badge color="secondary" badgeContent=" " variant="dot"></Badge>
            &nbsp;&nbsp;Food
          </Button>
          <Button size="small" style={{ color: 'grey' }}>
            <Badge
              color="secondary"
              style={{ color: 'red', background: 'red' }}
              badgeContent=" "
              variant="dot"
            ></Badge>
            &nbsp;&nbsp;Petrol
          </Button>
        </Grid>
      </Grid>
    </Card>
  );
}
